import LaReaderBook from './reader-book/index';
// 
const components = [
  LaReaderBook,
];
//  install  Vue 
// @ts-ignore
const install = (Vue) => {
  // 
  // @ts-ignore
  if (install.installed) { return; }
  // @ts-ignore
  install.installed = true;
  // 
  // @ts-ignore
  components.map(component => Vue.component(component.name, component));
  // 
};

//  Vue  Vue 
if (typeof window !== 'undefined' && window.Vue) {
  // @ts-ignore
  install(window.Vue);
}

export {
  LaReaderBook
};

export default {
  install,
  //  install Vue.use()
  ...components
};
